import {AContainer} from "./abstract/a-container";
import {Button} from "../button/button";
import {Badge} from "../button/badge";
import {Icon} from "../button/icon";
import {IRenderable} from "../core/interface/i-renderable";
import {EVerticalAlign} from "../component/enum/e-vertical-align";
export type IGroupItem =  Button|Badge|Icon;
import jquery = require("jquery");

/**
 * Este container agrupa um conjunto de botões.
 * 
 * <img src="../../doc_assets/groupbutton-example.png">
 * 
 * @example
 * import {Button} from "mvcomponents/button";
 * import {ViewPager, Box, ButtonGroup} from "mvcomponents/container";
 * import {Label} from "mvcomponents/widget";
 * import {LoginForm} from "./login-form";
 *
 * let ButtonGroupContainer: ButtonGroup = new ButtonGroup()
 *      .append(new Button()
 *          .setLabel("Adicionar")
 *          .setIcon("glyphicon glyphicon-plus"))
 *      .append(new Button()
 *          .setLabel("Editar")
 *          .setIcon("glyphicon glyphicon-edit"))
 *      .append(new Button()
 *          .setLabel("Excluir")
 *          .setIcon("glyphicon glyphicon-remove"))
 *      .append(new Button();
 *          .setLabel("Clique aqui para ver um alert()"));
 * 
 * @export
 * @memberof module:mvcomponents/container
 * @extends {AContainer}
 */
export class ButtonGroup extends AContainer<IGroupItem>{
	private defaultVerticalAlign:EVerticalAlign;
	constructor() {
		super("div", "");
		this.defaultVerticalAlign = EVerticalAlign.BOTTOM;
		jquery(this.element)
			.addClass("ButtonGroup col-sm-offset-0 col-sm-12 col-xs-12")
			.css({ "margin-bottom": "10px" });
	}
	/**
	 * Configura o alinhamento padrão dos botões dentro do {ButtonGroup}
	 * @param {EVerticalAlign} align
	 * @returns {ButtonGroup}
	 */
	public setDefaultInnerVerticalAlign(align:EVerticalAlign):this{
		this.defaultVerticalAlign = align;
		return this;
	}
	private prepareItem(item:IGroupItem):void{
		if((<Button>item).getVerticalAlign && (!(<Button>item).getVerticalAlign())){            
			(<Button>item).setVerticalAlign(this.defaultVerticalAlign);            
		}
	}
	/**
	 * Adiciona um item no accordion abaixo dos outros itens
	 * @returns {Accordion}
	 */    
	public append(item:IGroupItem):this{
		this.prepareItem(item);
		super.append(item);
		return this;
	}
	/**
	 * Adiciona um item no accordion acima dos outros itens
	 * @returns {Accordion}
	 */    
	public prepend(item:IGroupItem):this{
		this.prepareItem(item);
		super.prepend(item);
		return this;
	}
}
